.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #1d1d1d;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
}

.connect {
  width: 180px;
  margin: 20px 20px 0 auto;
  height: 40px;
  border-radius: 5px;
  cursor: pointer;
  background-color: #fee712;
  border: none;
  font-weight: 700;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.tokenIcon {
  margin: "0 10px";
}

.hero {
  margin-top: 100px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.yellow {
  color: #fff6b6;
}

.viewer {
  margin-top: 100px;
}

.viewer-balances {
  display: flex;
}

.viewer-item {
  padding: 1rem;
}
